/* enterUsedBudget.css */

.used-budget-form {
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  background-color: #fff;
}

body {
  background-image: url('../../src/background.png');
  background-size: cover;
  background-repeat: no-repeat;
}

body {
  overflow-y: auto;
}

h2 {
  color: #333;
}

form {
  display: flex;
  flex-direction: column;
}

label {
  margin-bottom: 10px;
}

select,
input {
  padding: 8px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 100%;
}

button {
  background-color: #007bff;
  color: #fff;
  padding: 10px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
}

button:hover {
  background-color: #0056b3;
}
.sidebar {
  height: 100%;
  width: 200px;
  // position: fixed;
  top: 0;
 
  left: 0;
  background-color: #111;
  padding-top: 20px;
}

.sidebar a {
  text-decoration: none;
  color: white;
  display: block;
  padding: 16px;
  text-align: center;
  font-size: 18px;
}

.sidebar a:hover {
  background-color: #ddd;
  color: black;
}